import { useProtectedRoutesApi } from "@/libraries/API/ProtectedRoute/secureRoutes";
import { useQuery, useMutation, useQueryClient } from "@tanstack/react-query";
import { useState } from "react";
import { Parent } from "../components/types";

export const useParentManagement = () => {
  const queryClient = useQueryClient();
  const { parentsList, updateParent, deleteParent } = useProtectedRoutesApi();
  const [searchTerm, setSearchTerm] = useState("");
  const [editableData, setEditableData] = useState<{ [key: string]: Parent }>(
    {}
  );

  const { data, isLoading, isError, error } = useQuery(
    ["parents"],
    parentsList
  );
  const updateMutation = useMutation(updateParent, {
    onSuccess: () => queryClient.invalidateQueries(["parents"]),
  });
  const deleteMutation = useMutation(deleteParent, {
    onSuccess: () => queryClient.invalidateQueries(["parents"]),
  });

  return {
    searchTerm,
    setSearchTerm,
    parents: data?.data || [],
    isLoading,
    isError,
    error,
    updateMutation,
    deleteMutation,
    editableData,
    setEditableData,
  };
};
