// export default ParentManagement;

"use client";

import Footer from "@/components/footer";
import Sidebar from "@/components/sidebar";
import SearchBar from "./components/SearchBar";
import ParentTable from "./components/ParentTable";
import { useParentManagement } from "./components/useParentManagement";

const ParentManagement = () => {
  const {
    searchTerm,
    setSearchTerm,
    parents,
    isLoading,
    isError,
    error,
    updateMutation,
    deleteMutation,
    handleEdit,
    handleSave,
    handleDelete,
    handleChange,
    handleAddressChange,
    editableData,
  } = useParentManagement();

  if (isLoading) return <div>Loading...</div>;
  if (isError) return <div>Error: {error.message}</div>;

  return (
    <div className="grid grid-cols-[250px_1fr] grid-rows-[1fr_auto] min-h-screen">
      <Sidebar />
      <main className="p-8 sm:p-20 flex flex-col gap-8">
        <div className="flex justify-between items-center">
          <h1 className="text-2xl font-bold">Parent Management</h1>
          <SearchBar value={searchTerm} onChange={setSearchTerm} />
        </div>
        <ParentTable
          parents={parents}
          editableData={editableData}
          onEdit={handleEdit}
          onSave={handleSave}
          onDelete={handleDelete}
          onChange={handleChange}
          onAddressChange={handleAddressChange}
          updateMutation={updateMutation}
          deleteMutation={deleteMutation}
        />
      </main>
      <Footer />
    </div>
  );
};

export default ParentManagement;
