import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { TableCell, TableRow } from "@/components/ui/table";
import { Parent } from "../components/types";

type ParentRowProps = {
  parent: Parent;
  editableData: { [key: string]: Parent };
  onEdit: (parent: Parent) => void;
  onSave: (parentId: string) => void;
  onDelete: (parentId: string) => void;
  onChange: (parentId: string, field: string, value: string) => void;
  onAddressChange: (parentId: string, field: string, value: string) => void;
  updateMutation: any;
  deleteMutation: any;
};

const ParentRow = ({
  parent,
  editableData,
  onEdit,
  onSave,
  onDelete,
  onChange,
  onAddressChange,
  updateMutation,
  deleteMutation,
}: ParentRowProps) => {
  const isEditing = !!editableData[parent.id];
  const currentParent = editableData[parent.id] || parent;

  return (
    <TableRow key={parent.id}>
      <TableCell>
        <Input
          value={currentParent.fullname}
          onChange={(e) => onChange(parent.id, "fullname", e.target.value)}
          disabled={!isEditing}
        />
      </TableCell>
      <TableCell>
        <Input
          value={currentParent.contact_number}
          onChange={(e) =>
            onChange(parent.id, "contact_number", e.target.value)
          }
          disabled={!isEditing}
        />
      </TableCell>
      <TableCell>
        <Input
          value={currentParent.auth.email}
          onChange={(e) => onChange(parent.id, "auth.email", e.target.value)}
          disabled={!isEditing}
        />
      </TableCell>
      <TableCell>
        <Input
          value={currentParent.address.purok}
          onChange={(e) => onAddressChange(parent.id, "purok", e.target.value)}
          disabled={!isEditing}
        />
      </TableCell>
      <TableCell>
        <div className="flex gap-2">
          {isEditing ? (
            <Button
              onClick={() => onSave(parent.id)}
              disabled={updateMutation.isPending}
            >
              {updateMutation.isPending ? "Saving..." : "Save"}
            </Button>
          ) : (
            <>
              <Button onClick={() => onEdit(parent)}>Edit</Button>
              <Button
                variant="destructive"
                onClick={() => onDelete(parent.id)}
                disabled={deleteMutation.isPending}
              >
                {deleteMutation.isPending ? "Deleting..." : "Delete"}
              </Button>
            </>
          )}
        </div>
      </TableCell>
    </TableRow>
  );
};

export default ParentRow;
