import {
  Table,
  TableBody,
  TableCaption,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table";
import ParentRow from "./ParentRow";
import { Parent } from "../components/types";

type ParentTableProps = {
  parents: Parent[];
  editableData: { [key: string]: Parent };
  onEdit: (parent: Parent) => void;
  onSave: (parentId: string) => void;
  onDelete: (parentId: string) => void;
  onChange: (parentId: string, field: string, value: string) => void;
  onAddressChange: (parentId: string, field: string, value: string) => void;
  updateMutation: any;
  deleteMutation: any;
};

const ParentTable = ({ parents, ...props }: ParentTableProps) => {
  return (
    <Table>
      <TableCaption>List of Registered Parents</TableCaption>
      <TableHeader>
        <TableRow>
          <TableHead>Full Name</TableHead>
          <TableHead>Contact Number</TableHead>
          <TableHead>Email</TableHead>
          <TableHead>Address</TableHead>
          <TableHead>Actions</TableHead>
        </TableRow>
      </TableHeader>
      <TableBody>
        {parents.length > 0 ? (
          parents.map((parent) => (
            <ParentRow key={parent.id} parent={parent} {...props} />
          ))
        ) : (
          <tr>
            <td colSpan={5} className="text-center py-4 text-gray-500">
              No parents found matching your search
            </td>
          </tr>
        )}
      </TableBody>
    </Table>
  );
};

export default ParentTable;
